tr {
    height: 50px;
}

th, td {
    padding: 10px;
    width: auto;
    min-width: 0;
    max-width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: wrap;
}

.table_head, .table_body {
    font-size: 0.8rem;
    text-align: center;
    
}

.table_head {
    color: rgb(0, 183, 255);
    border-bottom: 2px solid rgb(0, 183, 255);
}

.table_wrapper {
    width: 100%;
    height: 100%;
    overflow-x: auto;
}

.list_table {
    width: 100%;
    border: 0px;
}

.list_title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid;
}

.list_performance_check {
    display: block;
    position: sticky;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: normal;
    width: 80px;
}

.loading_spinner {
    position: absolute;
    text-align: center;
    margin: 0px;
    left: 50%;
    top: 50%;
}


.pagination {
    margin-left: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 40%;
    
}
